'use client';

import { useEffect, useState } from 'react';
import { DndContext, closestCorners, DragEndEvent } from '@dnd-kit/core';
import { SortableContext, horizontalListSortingStrategy, arrayMove } from '@dnd-kit/sortable';
import { List as ListType, Card as CardType, Board as BoardType } from '@/types';
import { List } from '@/components/dnd/List';
import { BoardSkeleton } from '@/components/dnd/BoardSkeleton';
import { Button } from '@/components/ui/Button';
import { useCsrf } from '@/hooks/useCsrf';
import { fetchWithCsrf } from '@/libs/fetch';

export default function Home() {
  const [boards, setBoards] = useState<BoardType[]>([]);
  const [activeBoard, setActiveBoard] = useState<BoardType | null>(null);
  const [lists, setLists] = useState<ListType[]>([]);
  const [cards, setCards] = useState<CardType[]>([]);
  const [loading, setLoading] = useState(true);
  const [loadingBoard, setLoadingBoard] = useState(false);
  const csrfToken = useCsrf();

  useEffect(() => {
    if (csrfToken) {
        fetch('/api/boards')
        .then(res => res.json())
        .then(data => {
            setBoards(data);
            if (data.length > 0) {
            setActiveBoard(data[0]);
            }
            setLoading(false);
        });
    }
  }, [csrfToken]);

  useEffect(() => {
    if (activeBoard && csrfToken) {
        setLoadingBoard(true);
      fetch(`/api/boards/${activeBoard._id}`)
        .then(res => res.json())
        .then(data => {
          setLists(data.lists);
          setCards(data.cards);
          setLoadingBoard(false);
        });
    }
  }, [activeBoard, csrfToken]);

  const handleCreateBoard = async () => {
    const name = prompt('Enter board name:');
    if (name) {
      const res = await fetchWithCsrf('/api/boards', {
        method: 'POST',
        body: JSON.stringify({ name }),
      });
      const newBoard = await res.json();
      setBoards([...boards, newBoard]);
      setActiveBoard(newBoard);
    }
  };

  const handleDragEnd = (event: DragEndEvent) => {
    const { active, over } = event;
    if (!over) return;

    if (active.id !== over.id) {
        // Dragging lists
        if (active.data.current?.type === 'List' && over.data.current?.type === 'List') {
            setLists((lists) => {
                const activeIndex = lists.findIndex(l => l._id === active.id);
                const overIndex = lists.findIndex(l => l._id === over.id);
                if (activeIndex === -1 || overIndex === -1) return lists;
                const newLists = arrayMove(lists, activeIndex, overIndex);
    
                // API call to update list position
                newLists.forEach((list, index) => {
                fetchWithCsrf(`/api/lists/${list._id}`, {
                    method: 'PUT',
                    body: JSON.stringify({ position: index }),
                });
                });
    
                return newLists;
            });
            return;
        }


      // Dragging cards
      if (active.data.current?.type === 'Card') {
        setCards((currentCards) => {
            const activeIndex = currentCards.findIndex(c => c._id === active.id);
            const overIndex = currentCards.findIndex(c => c._id === over.id);

            if (activeIndex === -1 || overIndex === -1) {
                return currentCards;
            }
            
            let newCards = arrayMove(currentCards, activeIndex, overIndex);
            
            const overListId = over.data.current?.sortable?.containerId || newCards[overIndex].list;

            if (overListId && newCards[activeIndex].list !== overListId) {
                newCards[activeIndex] = { ...newCards[activeIndex], list: overListId };
                newCards = arrayMove(newCards, activeIndex, overIndex);
            }

            // API call to update card position and list
            fetchWithCsrf(`/api/cards/${active.id}`, {
                method: 'PUT',
                body: JSON.stringify({
                    position: overIndex,
                    list: overListId,
                }),
            });
          
          return newCards;
        });
      }
    }
  };

  if (loading) {
      return (
        <div className="flex h-screen">
            <aside className="w-64 bg-gray-100 dark:bg-gray-800 p-4">
                <div className="h-8 w-32 bg-gray-200 dark:bg-gray-700 rounded-md mb-4 animate-pulse"></div>
                <div className="space-y-2">
                    <div className="h-10 bg-gray-200 dark:bg-gray-700 rounded-md animate-pulse"></div>
                    <div className="h-10 bg-gray-200 dark:bg-gray-700 rounded-md animate-pulse"></div>
                </div>
            </aside>
            <main className="flex-1 p-4">
                <BoardSkeleton />
            </main>
        </div>
      )
  }

  return (
    <div className="flex h-screen">
      <aside className="w-64 bg-gray-100 dark:bg-gray-800 p-4">
        <h2 className="text-lg font-bold mb-4">Boards</h2>
        <div className="space-y-2">
            {boards.map(board => (
                <Button key={board._id} variant={activeBoard?._id === board._id ? 'secondary' : 'ghost'} onClick={() => setActiveBoard(board)} className="w-full justify-start">
                    {board.name}
                </Button>
            ))}
        </div>
        <Button onClick={handleCreateBoard} className="mt-4 w-full">Create Board</Button>
      </aside>
      <main className="flex-1 p-4 overflow-x-auto">
      {loadingBoard ? (
        <BoardSkeleton />
      ) : activeBoard ? (
        <DndContext onDragEnd={handleDragEnd} collisionDetection={closestCorners}>
            <h1 className="text-2xl font-bold">{activeBoard.name}</h1>
          <SortableContext items={lists.map(l => l._id)} strategy={horizontalListSortingStrategy}>
            <div className="flex gap-4 mt-4">
              {lists.length > 0 ? (
                lists.map(list => (
                  <List key={list._id} list={list} cards={cards.filter(c => c.list === list._id)} />
                ))
              ) : (
                <div className="flex flex-col items-center justify-center h-full w-full">
                  <p className="text-gray-500">No lists yet. Create one to get started!</p>
                  {/* TODO: Add a button to create a new list */}
                </div>
              )}
            </div>
          </SortableContext>
        </DndContext>
      ) : (
        <div className="flex justify-center items-center h-full">
          <p>No boards found. Create one to get started!</p>
        </div>
      )}
      </main>
    </div>
  );
}