import Cookies from 'js-cookie';

export async function fetchWithCsrf(url: string, options: RequestInit = {}) {
  const csrfToken = Cookies.get('csrf-token');

  const headers = {
    ...options.headers,
    'Content-Type': 'application/json',
    'x-csrf-token': csrfToken || '',
  };

  return fetch(url, {
    ...options,
    headers,
  });
} 